@font-face {
  font-family: 'Dolce Vita Heavy A Few Extra Pounds';
  font-style: normal;
  font-weight: normal;
  src: local('Dolce Vita Heavy A Few Extra Pounds'), url('dolce vita heavy bold.woff') format('woff');
  }

.App {
  text-align: center;
}

.App .navbar-brand {
  font-weight: bold;
}
.App-header {
  background-color: #282c34;
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  /* font-size: calc(10px + 2vmin); */
  color: white;
}
.App-header h1{
  font-family:'Dolce Vita Heavy A Few Extra Pounds';
    font-weight:bold;
}
.row
{
  width: 100%;
  margin-left: 0;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

}
.button{
  color: White;
  height: 50px;
  width: 33.3333%;
  background-color: darkslategrey;
  border: none;
  transition: 0.5s;
  font-size:calc(4px + 2vmin);
  font-weight: bold;
  float: left;
  
}
.half{
  min-width: 50%;
}
.button:hover,.active{
  background-color: white;
  color: #282c34;
  cursor: pointer;

}

.container1{
  width: 500px;
  height: 1000px;
  /* box-shadow: 10px 10px 5px #333333; */
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  background-color: firebrick;
  display: flex;
  flex-direction: column;
  align-items: left;
  /* justify-content: center; */
}
#containword{
  min-height: 100px;
}
#containmean{
  text-align: left;
  font-size: calc(8px + 2vmin); 
  background-color: #282c34;
  transition: 0.5s;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
#containmean p{
  margin-left: calc(25px + 2vmin);
  margin-right: 10px;
}
#containmean h5{
  margin-left: 30px;
}

@media screen and (max-width: 600px)
{
  .container1{
    width: 100vw;
    height: 700px;
  }

  #addWords textarea{
    width: 80vw;
  }
}